        <div class="g-mtitle"></div>
        <div></div>
        <div class="g-mtitle"></div>
    </div>
</template>

<script>
export default {
    data () {
        return {}
    },
    methods: {
        HTMLEncode (html) {
            var temp = document.createElement('div')
            temp.textContent ? (temp.textContent = html) : (temp.innerText = html)
            var output = temp.innerHTML
            temp = null
            return output
        }
    }
}
</script>

