<template>
    <div class="article-modify">
        <u-layout dir="v" display="block">
            <u-input v-model="title" placeholder="" />
            <u-input v-model="content" type="textarea" placeholder="markdown" />
            <u-layout>
                <u-button color="primary" @click="modify"></u-button>
                <u-button to="/article"></u-button>
            </u-layout>
        </u-layout>
    </div>
</template>

<script>
import { addArticle, editArticleById, getArticleById } from '@/api'

export default {
    data () {
        return {
            title: '',
            content: ''
        }
    },
    computed: {
        id () {
            return this.$route.query.id
        }
    },
    mounted () {
        this.id && this.getArticleDetail()
    },
    methods: {
        getArticleDetail () {
            getArticleById(this.id).then(data => {
                this.title = data.title
                this.content = data.content
            })
        },
        modify () {
            this.id ? this.edit() : this.add()
        },
        add () {
            addArticle({
                title: this.title,
                content: this.content
            }).then(data => alert(''))
        },
        edit () {
            editArticleById({
                id: this.id,
                title: this.title,
                content: this.content
            }).then(data => alert(''))
        }
    }
}
</script>

<style lang="scss" scoped></style>
